.entry-list {
    li {
        list-style: none;
        margin-bottom: 1.333em;
        padding-bottom: 0.666em;
        border-bottom: thin solid #f3f3f3;
    }
    h3 {
        margin-top: .125em;
        font-weight: 600;
        font-size: 2.8rem;
        color: #222;
        a:focus,
        a:hover {
            color: #55acee;
        }
    }
} 